import React, { Fragment, useEffect, useState } from 'react'
import queryString from 'query-string'
import { useHistory, useLocation } from 'react-router-dom'
import axios from 'axios'
import './Buscar.css'
import { ArrowBarLeft, ArrowBarRight, ArrowDown, CheckCircleFill, PatchCheckFill } from 'react-bootstrap-icons'


export default function Buscar() {
    /////////////////////////////////////////////////////////////////// DECLARACION VARIABLES
    const options = {
        withCredentials: true,
        headers: {
            "Access-Control-Allow-Origin": "*",
            "Content-Type": "application/json",
        }
    }
    const url = "https://validacion.hgtsa.com.ar/lappa/transportista/buscadorget" // ?loc1=2&loc2=4&pagina=3
    const [data, setData] = useState({
        origen: "",
        destino: "",
        total: 0,
        pagina: 0,
        paginas: 0,
        transportistas: [{
            id: 0,
            web: "",
            razon: "",
            nombre: "",
            telefono: "",
            email: "",
            categoria: null
        }]
    })
    const { search } = useLocation()
    const history = useHistory()
    const qs = queryString.parse(search)
    ///////////////////////////////////////////////////////////////////////////////////////////// DECLARACION FUNCIONES
    useEffect(() => {
        async function fetchGetData() {
            await axios.get(url + "?loc1=" + qs.origen + "&loc2=" + qs.destino + "&pagina=" + qs.pagina, { options }).then(res => {
                console.log(res.data);
                setData(res.data)
            });
        }
        fetchGetData()
    }, [])
    const ArmarPagina = () => {
        return (<Fragment>
            {data.total > 0 ? <ArmarTransportistas lista={data.transportistas} /> : <p>No se encontraron resultados.</p>}
        </Fragment>)
    }
    const ArmarPaginas = ({ paginas }) => {
        var arr = []
        for (let i = 0; i < paginas; i++) {
            arr.push(i)
        }
        console.log(arr);
        return <div className="paginas">
            <a onClick={() => {
                if (qs.pagina > 1) {
                    setData({
                        origen: "",
                        destino: "",
                        total: 0,
                        pagina: 0,
                        paginas: 0,
                        transportistas: [{
                            id: 0,
                            web: "",
                            razon: "",
                            nombre: "",
                            telefono: "",
                            email: "",
                            categoria: null
                        }]
                    })
                    history.push("?origen=" + +qs.origen + "&destino=" + qs.destino + "&pagina=" + (parseInt(qs.pagina) - 1))
                    history.go(0)
                }
            }}><ArrowBarLeft /></a>
            {arr.map(numero => {
                return <a onClick={() => {
                    setData({
                        origen: "",
                        destino: "",
                        total: 0,
                        pagina: 0,
                        paginas: 0,
                        transportistas: [{
                            id: 0,
                            web: "",
                            razon: "",
                            nombre: "",
                            telefono: "",
                            email: "",
                            categoria: null
                        }]
                    })
                    history.push("?origen=" + +qs.origen + "&destino=" + qs.destino + "&pagina=" + (numero + 1))
                    history.go(0)
                }}>{numero + 1}</a>
            })}
            <a onClick={() => {
                if (qs.pagina < data.paginas) {
                    setData({
                        origen: "",
                        destino: "",
                        total: 0,
                        pagina: 0,
                        paginas: 0,
                        transportistas: [{
                            id: 0,
                            web: "",
                            razon: "",
                            nombre: "",
                            telefono: "",
                            email: "",
                            categoria: null
                        }]
                    })
                    history.push("?origen=" + +qs.origen + "&destino=" + qs.destino + "&pagina=" + (parseInt(qs.pagina) + 1))
                    history.go(0)
                }
            }}><ArrowBarRight /></a>
        </div>
    }
    const ArmarTransportistas = ({ lista }) => {
        console.log(lista);
        //////////////////////////////////////////////////////////////////////////////////////////////////////////
        const hola = {
            "id": 192,
            "web": false,
            "razon": false,
            "nombre": "AFORTUNADOS S.A.",
            "telefono": false,
            "email": false,
            "logo": "http://hgtarg.com/logo.jpg",
            "categoria": false
        }
        //////////////////////////////////////////////////////////////////////////////////////////////////////////
        return <div className='transportistasList'>
            {lista.map((item, index) => {
                // return item.categoria ?
                return item.nombre === "VIA CARGO" ? <div className='card row' onClick={() => {
                    history.push("/perfil?id_localidad_origen=2&id_localidad_destino=4&id_transportista=2")
                }}>
                    <img src={item.logo} alt={"Logo de " + item.nombre} />
                    <div className="col contenido">
                        <div className='nombre'>{item.nombre}</div>
                        <div className='telefono'>Telefono: {" " + item.telefono}</div>
                        <div className='web'>{item.web}</div>
                    </div>
                    <div>
                        {index <= 1 ? <CheckCircleFill size={"5vw"} style={{color: "#FF5100", border: ".7vw solid #f9b233",borderRadius: "10vw", marginLeft:".7vh"}} /> : undefined}
                    </div>
                </div> :
                <div className='card row'>
                    <img src={item.logo} alt={"Logo de " + item.nombre} />
                    <div className="contenido col">
                        <div className='nombre gris'>{item.nombre}</div>
                    </div>
                    <div style={{ minWidth: "5vh"}} />
                </div>
            })}
        </div>
    }
    return (
        <div className='container'>
            {data.origen === "" ? <p>CARGANDO RESULTADOS...</p> : <Fragment>
                <ArmarPagina />
                <ArmarPaginas paginas={data.paginas} />
            </Fragment>}
        </div>
    )
}